<template>
    <div class="commentLists">
        <ul>
            <li v-for="(comment, i) in commtentsLists?.data" :key="i">
                <div class="user">
                    <img :src="comment.user?.web_url" alt="">
                    <span class="user_name">{{comment.user?.user_name}}</span>
                    <span class="time">{{comment.created_at.split(' ')[0].split('-').join('.') +' '+
                    comment.created_at.split(' ')[1].slice(0,5)}}</span>
                </div>
                <div class="contents" v-if="comment.touser">
                    <div class="replied">
                        <span class="touser">{{comment.touser.user_name}}: </span>
                        <span>{{comment.quote}}</span>
                    </div>
                    <div class="ct">{{comment.content}}</div>
                </div>
                <div class="contents" v-else>
                    <div class="ct">{{comment.content}}</div>
                </div>
                <div class="btn">
                    <div class="reply"></div>
                    <div class="like">
                        <i v-show="!likedLists.map(o => o).includes(comment.id)" @click="addLiked(comment.id)"></i>
                        <i v-show="likedLists.map(o => o).includes(comment.id)" @click="removeLiked(comment.id)"></i>
                        <span>{{!likedLists.map(o => o).includes(comment.id) ? comment.praisenum : comment.praisenum +1}}</span>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props: {
        commtentsLists: Object,
    },
    data() {
        return {
            isLiked: false,
            likedLists: JSON.parse(localStorage.getItem('likedLists')) || [],
        };
    },
    methods: {
        addLiked: function (id) {
            this.likedLists = [...this.likedLists, id];
        },
        removeLiked: function (id) {
            this.likedLists = this.likedLists.filter(o => o !== id);
        },
    },
    watch: {
        likedLists: function (newLikedLists) {
            localStorage.setItem('likedLists', JSON.stringify(newLikedLists))
        }
    }
};
</script>

<style lang="less" scoped>
.commentLists {
    width: 100%;

    ul {

        li {
            width: 100%;
            border-bottom: 1rem solid rgb(221, 220, 220);

            .user {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-top: 10rem;

                img {
                    width: 20rem;
                    height: 20rem;
                    background-size: cover;
                    border-radius: 50%;
                }

                .user_name {
                    font-size: 14rem;
                    color: rgb(159, 158, 158);
                    flex-grow: 1;
                    margin-left: 8rem;
                }

                .time {
                    color: rgb(149, 147, 147);
                }
            }

            .contents {
                margin-top: 10rem;
                margin-left: 20rem;
                margin-bottom: 20rem;

                .replied {
                    border: 1rem solid rgb(227, 227, 227);
                    margin-bottom: 20rem;

                    span {
                        display: inline-block;
                        font-size: 14rem;
                        padding: 10rem;
                    }
                }

                .ct {
                    font-size: 14rem;
                }
            }

            .btn {
                width: 100%;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                margin-bottom: 20rem;

                .reply {
                    width: 16rem;
                    height: 16rem;
                    background-image: url(../assets/comment_image.png);
                    background-size: cover;
                    margin-right: 25rem;
                }

                .like {
                    width: 40rem;
                    height: 16rem;
                    position: relative;
                    margin-right: 20rem;

                    i {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 16rem;
                        height: 16rem;
                        background-size: cover;

                        &:nth-child(1) {
                            background-image: url(../assets/comment_laud.png);
                        }

                        &:nth-child(2) {
                            background-image: url(../assets/comment_laud_selected.png);
                        }
                    }

                    span {
                        display: inline-block;
                        margin-top: 2rem;
                        margin-left: 25rem;
                        color: #a3a3a3;
                    }
                }
            }
        }
    }
}
</style>
