<template>
    <div class="search">
        <div class="top">
            <input type="text" id="input" placeholder="" v-model="keyword"
                @keyup.enter="getResultLists(); showResult=true">
            <span @click="$router.back(); showResult=false"></span>
        </div>
        <div class="type" v-if="!showResult">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
        </div>
        <div class="list" v-if="showResult">
            <ul>
                <li v-for="(item, i) in categoryName" :key="i" :class="{active: i===current}"
                    @click="current=i;category=item.type;getResultLists(item.type, keyword)">
                    {{item.name}}
                </li>
            </ul>
        </div>
        <div class="result" v-if="showResult">
            <ul v-if="resultLists.length">
                <li v-for="(item, i) in resultLists" :key="i"
                    @click="$router.push({path: '/article', query: {type: item.category, articleId: item.content_id, date: item.date.split(' ')[0]}}); isShowArticle()">
                    <img :src="item.cover" alt="">
                    <div class="intro">
                        <h3>{{item.title}}</h3>
                        <span>{{item.subtitle}}</span>
                    </div>
                </li>
            </ul>
            <div class="null" v-if="notFound">
                <img src="../assets/notSearch.png" alt="">
            </div>
        </div>

        <div class="locadingmask" v-if="showLoading">
            <div class="locading"></div>
        </div>
    </div>
</template>

<script>
import { getSearchResult } from "@/apis";
import { mapMutations, mapState } from 'vuex';
export default {
    props: {},
    data() {
        return {
            resultLists: [],
            category: 'hp',
            keyword: '',
            showResult: false,
            categoryName: [
                {
                    name: '',
                    type: 'hp',
                },
                {
                    name: '',
                    type: 'reading',
                },
                {
                    name: '',
                    type: 'music',
                },
                {
                    name: '',
                    type: 'movie',
                },
                {
                    name: 'ONE',
                    type: 'radio',
                },
                {
                    name: '/',
                    type: 'author',
                },
            ],
            current: 0,
            notFound: false,
        };
    },
    computed: {
        ...mapState(['showArticle', 'showLoading'])
    },
    methods: {
        getResultLists: function (category, keyword) {
            // console.log(this.keyword);
            category = this.category;
            keyword = this.keyword;
            this.isShowLoading();
            getSearchResult(category, keyword)
                .then(res => {
                    // console.log(res);
                    this.resultLists = res.data?.data.list;

                    if (!this.resultLists.length) {
                        this.notFound = true;
                    } else {
                        this.notFound = false;
                    }
                    this.closeshowLoading();
                });
            // console.log(category, keyword);
        },

        ...mapMutations(['isShowArticle', 'isShowLoading', 'closeshowLoading'])
    }
};
</script>

<style lang="less" scoped>
.search {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background-color: white;
    z-index: 9;

    .top {
        width: 100%;
        height: 50rem;
        background-color: #ebeaea66;
        position: fixed;
        top: 0;
        left: 0;

        #input {
            border: none;
            margin-left: 15rem;
            width: 300rem;
            height: 30rem;
            padding-left: 10rem;
            margin-top: 10rem;
            font-size: 13rem;
        }

        span {
            margin-left: 10rem;
        }
    }

    .type {
        width: 100%;
        height: 70%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        margin-top: 30%;
        font-size: 14rem;
        color: rgb(144, 144, 144);
    }

    .list {
        padding-left: 15rem;
        background-color: rgb(254, 255, 255);
        position: fixed;
        top: 60rem;
        left: 0;

        ul {
            position: relative;
            width: 90%;
            overflow: auto;
            display: flex;
            justify-content: space-between;
            align-items: center;

            &::-webkit-scrollbar {
                display: none;
                width: 0 !important;
                height: 0 !important;
                -webkit-appearance: none;
                background: transparent;
            }

            li {
                font-size: 14rem;
                margin-right: 30rem;
                padding-bottom: 5rem;
                color: rgb(168, 167, 167);
                white-space: nowrap;

                &.active {
                    border-bottom: 2rem solid black;
                    color: black;

                }
            }
        }
    }

    .result {
        width: 96%;
        height: 85vh;
        margin-left: 15rem;
        margin-top: 90rem;
        overflow: auto;

        &::-webkit-scrollbar {
            display: none;
            width: 0 !important;
            height: 0 !important;
            -webkit-appearance: none;
            background: transparent;
        }

        ul {
            width: 100%;

            li {
                width: 100%;
                display: flex;
                align-items: center;
                margin-top: 10rem;
                padding-bottom: 12rem;
                border-bottom: 1rem solid rgb(230, 228, 228);

                img {
                    width: 40rem;
                    height: 40rem;
                    background-size: cover;
                }

                .intro {
                    width: 275rem;
                    margin-left: 15rem;

                    h3 {
                        width: 100%;
                        font-size: 16rem;
                        margin: 0;
                        padding: 0;
                        margin-bottom: 5rem;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        word-break: normal;
                        font-weight: normal;
                    }

                    span {
                        color: rgb(179, 178, 178);
                    }
                }
            }
        }
    }

    .null {
        position: absolute;
        top: 190rem;
        left: 60rem;

        img {
            width: 250rem;
            height: 250rem;
            background-size: cover;
        }
    }
}

@keyframes xxx {
    from {
        background-position-x: 0;
    }

    to {
        background-position-x: -2002px;
    }
}

.locadingmask {
    position: fixed;
    top: 0rem;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #fff;
    z-index: 999;

    .locading {
        width: 154rem;
        height: 155rem;
        background-image: url("../assets/_20221007141751.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        animation-name: xxx;
        animation-duration: 1s;
        animation-iteration-count: infinite;
        /* animation-timing-function: linear; */
        animation-timing-function: steps(13, end);
        margin-top: 278rem;
        margin-left: 125rem;

    }
}
</style>
