import Vue from 'vue';
import Vuex from 'vuex';
// import { getRecommend } from "@/apis";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    showArticle: false, // 
    showThumb: false, // 
    isShare: false,  // 
    playing: false,
    audioSrc: JSON.parse(localStorage.getItem('audioSrc')) || null,  // 
    audio: JSON.parse(localStorage.getItem('audio')) || null,  //  
    audioList: JSON.parse(localStorage.getItem('audioList')) || [],  //  
    audioIDList: JSON.parse(localStorage.getItem('audioIDList')) || [], // ID
    audioID: JSON.parse(localStorage.getItem('audioID')) || null,  //  ID
    showAudioTip: false, //  
    duration: 0,  //  
    currentTime: Number(localStorage.getItem("currentTime")) || 0,  // 
    PlayInterface: false,  //  
    // showDairy: false,  // 
    articleLikedID: JSON.parse(localStorage.getItem('articleLikedID')) || [],  //  ID
    articleCollectionID: JSON.parse(localStorage.getItem('articleCollectionID')) || [],  //  ID
    articleCollection: JSON.parse(localStorage.getItem('articleCollection')) || [],  //  
    graphicLikedID: JSON.parse(localStorage.getItem('graphicLikedID')) || [],  //  ID
    collectionList: JSON.parse(localStorage.getItem('collectionList')) || [],  //  ID
    showLoading: false,  //   loading
    indiList: JSON.parse(localStorage.getItem('indiList')) || [],  //  
    authorFansID: JSON.parse(localStorage.getItem('authorFansID')) || [],  //  ID
    authorFansList: JSON.parse(localStorage.getItem('authorFansList')) || [],  //  
  },
  getters: {

  },
  mutations: {
    isShowArticle(state) {
      state.showArticle = true;
    },
    backArticle(state) {
      state.showArticle = false;
    },
    isShowThumb(state) {
      state.showThumb = true;
    },
    closeThumb(state) {
      state.showThumb = false;
    },
    isShowShare(state) {
      state.isShare = true;
    },
    closeShare(state) {
      state.isShare = false;
    },
    getAudioSrc(state, { src, audio }) {
      state.audioSrc = src;
      state.audio = audio;
    },
    audioPlay(state) {
      state.playing = true;
    },
    audioPause(state) {
      state.playing = false;
    },
    getAudioDuaration(state, { d }) {
      state.duration = d;
    },
    getAudioCurrenTime(state, { t }) {
      state.currentTime = t;
    },
    showPlayInterface(state) {
      state.PlayInterface = true;
    },
    closePlayInterface(state) {
      state.PlayInterface = false;
    },

    // isShowDairy(state) {
    //   state.showDairy = true;
    // },
    // closeshowDairye(state) {
    //   state.showDairy = false;
    // },

    addArticleLikedID(state, { id }) {
      state.articleLikedID = [...state.articleLikedID, id];
    },
    removeArticleLikedID(state, { id }) {
      state.articleLikedID = state.articleLikedID.filter(o => o !== id);
    },

    addGraphicLikedID(state, { id }) {
      state.graphicLikedID = [...state.graphicLikedID, id];
    },
    removeGraphicLikedID(state, { id }) {
      state.graphicLikedID = state.graphicLikedID.filter(o => o !== id);
    },

    changeCurrentTime(state, { newTime }) {
      state.currentTime = newTime;
      console.log(newTime);
    },

    addCollectionList(state, { id, list }) {
      state.collectionList = [...state.collectionList, id];
      state.indiList = [...state.indiList, list];
      // state.indiList = state.indiList.unshift(list);
    },
    removeCollectionList(state, { id }) {
      state.collectionList = state.collectionList.filter(o => o !== id);
      state.indiList = state.indiList.filter(o => o.id !== id);
    },

    isShowLoading(state) {
      state.showLoading = true;
    },
    closeshowLoading(state) {
      state.showLoading = false;
    },

    articleCollList(state, { id, list }) {
      state.articleCollectionID = [...state.articleCollectionID, id];
      state.articleCollection = [...state.articleCollection, list];
    },
    removeArticleCollectionID(state, { id }) {
      console.log(id);
      state.articleCollectionID = state.articleCollectionID.filter(o => o !== id);
      state.articleCollection = state.articleCollection.filter(o => o.id !== id);
    },

    addAuthorFans(state, { id, list }) {
      state.authorFansID = [...state.authorFansID, id];
      state.authorFansList = [...state.authorFansList, list];
    },
    removeAuthorFans(state, { id }) {
      state.authorFansID = state.authorFansID.filter(o => o !== id);
      state.authorFansList = state.authorFansList.filter(o => o.user_id !== id);
    },

    addAudioList(state, { id, list, type }) {
      if (type === 4) {
        state.audioIDList = [...state.audioIDList, id];
        state.audioList = [...state.audioList, list];
      } else {
        state.showAudioTip = true;
      }
    },
    removeAudioList(state, { id }) {
      state.audioIDList = state.audioIDList.filter(o => o !== id);
      state.audioList = state.audioList.filter(o => o.id !== id);
    },
    closeAudioTip(state) {
      state.showAudioTip = false;
    },

    changeAudio(state, { dir, model }) {
      if (state.audioList.length) {
        if (model === '') {
          var currentAudioIndex = state.audioList.findIndex(
            (o) => o.id === state.audio.id
          );
          if (dir === "next") {
            state.audio =
              currentAudioIndex === state.audioList.length - 1
                ? state.audioList[0]
                : state.audioList[currentAudioIndex + 1];
            state.audioID = state.audio.id;
            state.audioSrc = state.audio.json_content.audio_url;
          } else {
            state.audio =
              currentAudioIndex === 0
                ? state.audioList[state.audioList.length - 1]
                : state.audioList[currentAudioIndex - 1];
            state.audioID = state.audio.id;
            state.audioSrc = state.audio.json_content.audio_url;
          }
        }

        if (model === '') {
          state.audio.currentTime = 0;
          state.audioSrc = state.audio.json_content.audio_url;
        }
      }
      console.log(state.audio);
    },

    change_A_id(state, { id }) {
      state.audioID = id;
    },
    pause_A_id(state) {
      state.audioID = '';
    }
  },
  actions: {

  },
  modules: {
  }
});
