<template>
  <div class="home">
    <div class="h-head">
      <div class="weather" @click="showOtherDays=!showOtherDays">
        <div class="date" v-if="homeDate">
          <span>{{Number(homeDate[2])>9?(Number(homeDate[2])):'0'+(Number(homeDate[2]))}}</span>
          <!-- <span>{{Number(homeDate[1]).toDateString()}}</span> -->
          <span>{{new Date().toDateString().split(' ')[1]}}.</span>
          <span>{{homeDate[0]}}</span>
          <img src="../assets/hp_triangle.png" @click="isTriangle=!isTriangle" :class="{rotate: isTriangle}" alt="" />
        </div>
        <div class="info">
          <span>{{dayRecommend.weather?.city_name}}  {{dayRecommend.weather?.climate}}</span>
          <span> {{dayRecommend.weather?.temperature}}</span>
        </div>
      </div>
      <!--  -->
      <div class="h_radio" v-if="!showOtherDays || showRadio"
        @click="$router.push({path: '/article', query: {type:contentList[4]?.category, articleId: contentList[4]?.content_id, src: contentList[4]?.audio_url}})">
        <div class="radio_btn">
          <img src="../assets/ani_radio/fm_icon03.png" alt="">
        </div>
        <div class="diantai">
          <marquee behavior="scroll" direction="left">
            <h3>
              ONE-{{contentList[4]?.author.user_name}}{{contentList[4]?.title}}
            </h3>
          </marquee>
        </div>
        <!-- <div class="close_radio"></div> -->
        <div class="close_radio" @click.stop="showRadio=false"></div>
      </div>
      <!-- </transition> -->
    </div>
    <transition name="fade">
      <keep-alive>
        <HomeRecommend :contentList="contentList" v-if="!showOtherDays" @audio-play="$emit('audio-play')" @audio-pause="$emit('audio-pause')"></HomeRecommend>
      </keep-alive>
    </transition>
    <OtherDatesRecommend v-if="showOtherDays" :showOtherDays="showOtherDays" @change-date="getRecommendLists($event)"
      @close-otherDates="showOtherDays=$event">
    </OtherDatesRecommend>
  </div>
</template>

<script>
// @ is an alias to /src
import HomeRecommend from '@/components/HomeRecommend.vue';
import OtherDatesRecommend from '@/components/OtherDatesRecommend.vue';
import { getRecommend } from "@/apis";
import { mapMutations, mapState } from 'vuex';
// import { getEveryDays } from "@/apis";
export default {
  name: 'HomeView',
  components: {
    HomeRecommend,
    OtherDatesRecommend
  },
  data: function () {
    return {
      dayRecommend: {},
      homeDate: null,
      isTriangle: false,
      contentList: [],
      currentDate: new Date().getFullYear() + '-' + (new Date().getMonth() + 1),
      showOtherDays: false,
      showRadio: true,
    };
  },
  computed: {
    ...mapState(['showLoading'])
  },
  methods: {
    // ...mapMutations(["getRecommendLists"])
    // changeDate: function (value) {
    //   getEveryDays(value).then(res => {
    //     this.everyDayLists = res.data.data;
    //     console.log(value);
    //   });
    // }
    ...mapMutations(['getAudioSrc']),

    getRecommendLists: function (date = 0) {
      getRecommend(date).then(res => {
        this.dayRecommend = res.data.data;
        this.contentList = this.dayRecommend.content_list;
        this.homeDate = this.contentList[0].post_date.split(' ')[0].split('-');

      });
    },


  },
  created() {
    this.getRecommendLists();
  },
  beforeRouteUpdate() {
    this.getRecommendLists(this.$router.query?.date ? this.$router.query.date : 0);
  }
};
</script>

