<script>
import { getComments, getGraphicDetails } from "@/apis";
import CommentLists from '@/components/CommentLists.vue';
import { mapMutations, mapState } from "vuex";
export default {
    props: {},
    components: {
        CommentLists
    },
    data() {
        return {
            articleContent: {},
            commtentsLists: {},
            category: null,
            isGraphic: false,
            graphicDetail: {},
            articleLiked: JSON.parse(localStorage.getItem('articleLiked')) || false,
            playCount: 1,
            movieImg: null,
            showSerial_date: false,
            serial_date_list: [],
            current_serial_date: 0
        };
    },
    computed: {
        ...mapState(['showThumb', 'isShare', 'playing', 'duration', 'articleLikedID', 'graphicLikedID', 'collectionList', 'showLoading', 'articleCollectionID', 'articleCollection', 'authorFansID', 'authorFansList', 'audioID']),
    },
    methods: {
        ...mapMutations(['backArticle', 'isShowThumb', 'isShowShare', 'closeThumb', 'closeShare', 'audioPlay', 'audioPause', 'getAudioSrc', 'addArticleLikedID', 'removeArticleLikedID', 'addGraphicLikedID', 'removeGraphicLikedID', 'addCollectionList', 'removeCollectionList', 'isShowLoading', 'closeshowLoading', 'isShowArticle', 'articleCollList', 'removeArticleCollectionID', 'addAuthorFans', 'removeAuthorFans', 'change_A_id', 'pause_A_id']),
        getArticlesLists: function (type, articalId, date, img) {
            // console.log(type, articalId);
            switch (type) {
                case '0':
                    this.isShowLoading();
                    // console.log(type, articalId);
                    getGraphicDetails(date).then(res => {
                        this.isGraphic = true;
                        this.graphicDetail = res.data.data;
                        // console.log(res.data.data);
                        this.closeshowLoading();

                    });
                    break;
                case '1':
                    this.category = "essay";
                    this.isGraphic = false;
                    break;
                case '3':
                    this.category = "question";
                    this.isGraphic = false;
                    break;
                case '4':
                    this.category = "music";
                    this.isGraphic = false;
                    break;
                case '5':
                    this.category = "movie";
                    this.movieImg = img;
                    this.isGraphic = false;
                    break;
                case '8':
                    this.category = "radio";
                    this.isGraphic = false;
                    // console.log(type, articalId);
                    break;
                case '11':
                    this.category = "topic";
                    this.isGraphic = false;
                    break;
                default:
                    this.category = type;
                    this.isGraphic = false;
                    // console.log(type, articalId);
                    break;
            }
            if (type !== '0') {
                this.isShowLoading();
                this.axios.get("https://apis.netstart.cn/one/" + this.category + "/htmlcontent/" + articalId).then(res => {
                    this.articleContent = res.data.data;
                    this.closeshowLoading();
                });
            }
        },
        getCommentsLists: function (type, articleId) {
            switch (type) {
                case '1':
                    this.category = "essay";
                    break;
                case '3':
                    this.category = "question";
                    break;
                case '4':
                    this.category = "music";
                    break;
                case '5':
                    this.category = "movie";
                    break;
                case '8':
                    this.category = "radio";
                    break;
                case 'serialcontent':
                    this.category = "serial";
                    break;
                // default:
                //     this.category = type;
                //     break;
            }
            getComments(this.category, articleId).then(res => {
                this.commtentsLists = res.data.data;
            });
        },
        clearArticleContent: function () {
            this.articleContent = {};
        },
        isPlaying: function () {
            this.playCount++;
            if (this.playCount % 2 === 0) {
                this.audioPause();
            } else {
                this.audioPlay();
            }
        },
        //
        goRule(event) {
            var id = "#" + event;
            document.querySelector(id).scrollIntoView({
                behavior: "smooth",
                block: "center",
                inline: "nearest",
            });
        },
        // changeContent(id) {
        //     console.log(id);
        //     this.$router.push({ query: { articleId: id } });
        // }
    },
    created() {
        // this.isShowLoading();
        this.isShowArticle();
        // console.log(this.showLoading);
        this.getArticlesLists(this.$route?.query.type, this.$route.query.articleId, this.$route.query.date, this.$route.query.img, this.$route.query.audio_url);
        this.getCommentsLists(this.$route?.query.type, this.$route.query.articleId);
        // this.getAudioSrc({ src: this.articleContent?.audio !== '' ? this.articleContent?.audio : this.articleContent?.json_content.audio_url, audio: this.articleContent });
        this.serial_date_list = this.$route.query.date_list;
        // console.log(this.serial_date_list);
    },
    beforeRouteUpdate(to, from, next) {
        this.getArticlesLists(this.$route?.query.type, this.$route.query.articleId, this.$route.query.date, this.$route.query.img);
        // this.getAudioSrc({ src: this.articleContent?.audio !== '' ? this.articleContent?.audio : this.articleContent?.json_content.audio_url, audio: this.articleContent });
        next();
    },
    watch: {
        articleLiked: function (articleLiked) {
            localStorage.setItem('articleLiked', JSON.stringify(articleLiked));
        },
        // $route: function (nRoute) {
        //     this.getArticlesLists('1', nRoute.query.articleId);
        //     this.getCommentsLists('1', nRoute.query.articleId);
        // }
    },
};
</script>

<style lang="less">
@keyframes xxx {
    from {
        background-position-x: 0;
    }

    to {
        background-position-x: -2002px;
    }
}

.locadingmask {
    position: fixed;
    top: 0rem;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #fff;
    z-index: 999;

    .locading {
        width: 154rem;
        height: 155rem;
        background-image: url("../assets/_20221007141751.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        animation-name: xxx;
        animation-duration: 1s;
        animation-iteration-count: infinite;
        /* animation-timing-function: linear; */
        animation-timing-function: steps(13, end);
        margin-top: 278rem;
        margin-left: 125rem;

    }
}


.weather {
    height: 50rem;
    // margin: 10rem;
    display: flex;
    justify-content: space-between;
    color: #b1b1b1;
    box-sizing: border-box;
    background-color: white;

    .date {
        color: black;
        font-weight: bold;
        position: relative;
        margin-top: 14rem;
        margin-left: 20rem;

        span {

            &:nth-child(1) {
                font-size: 34rem;
                padding-right: 4rem;
            }
        }

        img {
            width: 10rem;
            height: 6rem;
            margin-left: 5rem;
            transition: all .3s;

            &.rotate {
                transform: rotate(180deg);
                animation: triangle .3s linear;
            }
        }
    }

    .info {
        padding-top: 30rem;
        margin-right: 10rem;

        span {
            margin-right: 5rem;
        }
    }
}

@keyframes playingrunning {
    from {
        transform: rotateX(15deg);
    }

    to {
        transform: rotateX(60deg);
    }
}

.radio_line {
    width: 100rem;
    height: 10rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .r_line {
        display: block;
        width: 2rem;
        height: 10rem;
        background-color: white;
        margin-left: 6rem;
        animation: playingrunning 0.4s infinite alternate;
        animation-play-state: paused;
        transform-origin: center;

        &:nth-child(1) {
            height: 5rem;
            animation: none;
        }

        &:nth-child(2) {
            height: 5rem;
            animation: none;
        }

        &:nth-child(8) {
            height: 5rem;
            animation: none;
        }

        &:nth-child(9) {
            height: 5rem;
            animation: none;
        }

        &:nth-child(10) {
            height: 5rem;
            animation: none;
        }

        &:nth-child(3) {
            height: 6rem;
            animation-delay: -1.2s;
        }

        &:nth-child(4) {
            animation-delay: -1.2s;
        }

        &:nth-child(5) {
            height: 12rem;
            animation-delay: -0.2s;
        }

        &:nth-child(6) {
