<template>
    <div class="serial">
        <div class="top" v-if="!showChapterLists">
            <ul class="yearList">
                <li v-for="(year, i) in years" :key="i" :class="{current: i===current}"
                    @click="current=i;changeSerialList(year)">{{year}}</li>
            </ul>
            <div class="search" @click="$router.push({path: '/search'})"></div>
        </div>
        <transition name="ani_found">
            <ul class="content_list" v-if="!showChapterLists">
                <li v-for="serial in serialList" :key="serial.id"
                    @click="showChapterLists=true; getFoundSerialList(serial.serial_id,serial.title),isShowArticle()">
                    <!-- ,isShowArticle() -->
                    <div class="content">
                        <div class="finish"># {{serial.finished===true? "" : ""}}
                        </div>
                        <!-- ?imageView2/0/w/250/h/100/q/75 -->
                        <img class="pc" :src="`${serial?.cover}`" alt="">
                        <h3>{{serial.title}}</h3>
                        <div class="bottom">
                            <p>{{serial.forward}}</p>
                            <span>{{serial.subtitle}}</span>
                        </div>
                    </div>
                </li>
            </ul>
        </transition>
        <div class="chapterLists" v-if="showChapterLists">
            <div class="title">
                <i @click="showChapterLists=false,backArticle()"></i>
                <h2>{{chapterListsName}}</h2>
            </div>
            <keep-alive>
                <ChapterLists :chapterLists="chapterLists"></ChapterLists>
            </keep-alive>
        </div>
        <div class="locadingmask" v-if="showLoading">
            <div class="locading"></div>
        </div>
    </div>
</template>

<script>
import ChapterLists from '@/components/ChapterLists.vue';
import { getSerialList, FoundSerialList } from "@/apis";
import { mapMutations, mapState } from 'vuex';
export default {
    props: {},
    components: {
        ChapterLists
    },
    data() {
        return {
            current: 0,
            years: [2022, 2021, 2020, 2019, 2018, 2017, 2016],
            serialList: [],
            showChapterLists: false,
            chapterLists: [],
            chapterListsName: null,
        };
    },
    computed: {
        ...mapState(['showLoading'])
    },
    methods: {
        changeSerialList: function (year) {
            this.isShowLoading();
            getSerialList(year).then(res => {
                this.serialList = res.data.data;
                this.closeshowLoading();
            });
        },
        getFoundSerialList(id, title) {
            FoundSerialList(id).then(res => {
                this.isShowLoading();
                // console.log(res.data.data);
                this.chapterLists = res.data.data;
                this.chapterListsName = title;
                this.closeshowLoading();
            });
        },
        ...mapMutations(['isShowLoading', 'closeshowLoading', 'isShowArticle','backArticle'])
    },
    created() {
        this.isShowLoading();
        getSerialList().then(res => {
            // console.log(res.data.data);
            this.serialList = res.data.data;
            this.closeshowLoading();
        });
    }
};
</script>

<style lang="less" scoped>
h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
}

@keyframes xxx {
    from {
        background-position-x: 0;
    }

    to {
        background-position-x: -2002px;
    }
}

.locadingmask {
    position: fixed;
    top: 0rem;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #fff;
    z-index: 999;

    .locading {
        width: 154rem;
        height: 155rem;
        background-image: url("../assets/_20221007141751.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        animation-name: xxx;
        animation-duration: 1s;
        animation-iteration-count: infinite;
        /* animation-timing-function: linear; */
        animation-timing-function: steps(13, end);
        margin-top: 278rem;
        margin-left: 125rem;

    }
}

@keyframes foundList {
    from {
        transform: translateX(0);
    }

    to {
        transform: translateX(667rem);
    }
}

// .ani_found-enter-active {

// }

.serial {
    width: 100%;
    height: 100vh;
    overflow: auto;
    position: relative;

    &::-webkit-scrollbar {
        display: none;
        width: 0 !important;
        height: 0 !important;
        -webkit-appearance: none;
        background: transparent;
    }

    .top {
        width: 100%;
        position: fixed;
        top: 0;
        left: 0;
        background-color: #fff;
        z-index: 9;
        padding-bottom: 15rem;

    }

    .yearList {
        display: flex;
        justify-content: space-between;
        width: 90%;
        overflow: auto;

        &::-webkit-scrollbar {
            display: none;
            width: 0 !important;
            height: 0 !important;
            -webkit-appearance: none;
            background: transparent;
        }

        li {
            color: #919191;
            padding: 6rem 10rem 6em 10rem;
            background-color: #f5f5f5;
            border-radius: 20rem;
            text-align: center;
            margin: 10rem 7rem 20rem 20rem;

            &.current {
                background-color: #363636;
                color: #f9f9f9;
            }
        }
    }

    .search {
        width: 20rem;
        height: 20rem;
        background-image: url(../assets/discover_icon_search.png);
        background-size: cover;
        position: absolute;
        top: 12rem;
        right: 15rem;
    }
}

.content_list {
    width: 335rem;
    margin-left: 20rem;
    margin-top: 70rem;

    li {
        width: 100%;
        // overflow: hidden;
        margin-bottom: 30rem;

        .content {
            width: 100%;
            position: relative;

            .finish {
                position: absolute;
                top: 10rem;
                left: 15rem;
                color: rgb(218, 214, 214);
            }

            .pc {
                width: 330rem;
                height: 190rem;
                border-top-left-radius: 10rem;
                border-top-right-radius: 10rem;
                display: block;
            }

            h3 {
                width: 97%;
                padding-bottom: 15rem;
                padding-left: 5rem;
                font-size: 18rem;
                color: white;
                position: absolute;
                bottom: 60rem;
                left: 0;
                font-weight: normal;
                background-image: linear-gradient(#77767634, rgba(12, 12, 12, 0.355));
            }

            .bottom {
                width: 330rem;
                padding-left: 10rem;
                padding-bottom: 10rem;
                background-color: white;
                box-shadow: 0 0 3rem 1rem rgb(183, 182, 182);
                box-sizing: border-box;
                border-bottom-left-radius: 10rem;
                border-bottom-right-radius: 10rem;


                p {
                    padding-top: 10rem;
                    padding-bottom: 5rem;
                    width: 330rem;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    word-break: normal;
                    font-size: 14rem;
                    color: #8f8f8f;
                }

                span {
                    color: #c6c6c6;
                }
            }
        }
    }
}

.chapterLists {
    width: 100%;
    height: 100vh;
    overflow: auto;
    background-color: white;
    position: relative;
    z-index: 9;

    &::-webkit-scrollbar {
        display: none;
        width: 0 !important;
        height: 0 !important;
        -webkit-appearance: none;
        background: transparent;
    }

    .title {
        position: relative;
        width: 100%;
        margin-bottom: 30rem;

        i {
            position: absolute;
            top: 0rem;
            left: 10rem;
            width: 25rem;
            height: 30rem;
            background-image: url(../assets/aliwx_common_back_btn_normal.png);
            background-size: cover;
        }

        h2 {
            font-size: 16rem;
            font-weight: normal;
            color: #393939;
            margin-left: 35rem;
            padding-top: 6rem;
        }
    }
}
</style>
