                <div class="way">
                    <img src="../assets/bubble_wechat.png" alt="">
                    <img src="../assets/bubble_weibo.png" alt="">
                    <img src="../assets/bubble_qq.png" alt="">
                </div>
            </div>
        </div>

        <div class="check_tips" v-show="showTips">
            /
        </div>

        <div class="check_tips" v-show="!showTips && z > 0">
            /
        </div>

        <div class="login_process" v-if="login && isChecked">
            <div class="login_pc">
                <i @click="login=false"></i>
