                        <i class="phone_close" v-show="phoneNmuber" @click="phoneNmuber=''"></i>
                    </div>
                    <div class="password_box">
                        <i class="password_icon"></i>
                        <input :type="passwordType" name="" id="password" placeholder="" v-model="passwordNmuber">
                        <i class="password_close" v-show="passwordNmuber" @click="passwordNmuber=''"></i>
                        <i class="password_invisible" v-if="!passwordVisible"
                            @click="passwordVisible=!passwordVisible,passwordType='text'"></i>
                        <i class="password_visible" v-else
                            @click="passwordVisible=!passwordVisible,passwordType='password'"></i>
                    </div>
                    <h3>?</h3>
                </div>
                <div class="check_box">
                    <div class="check_btn">
                        <i class="uncheck" v-if="!check" @click="check=!check"></i>
                        <i class="checked" v-else @click="check=!check"></i>
                    </div>
                    <div class="tips">
                        
                        <span></span>
                        
                        <span></span>
                    </div>
