<script>
// import { setTimeout } from 'timers/promises';
// import 'vant/es/toast/style';
export default {
    props: {},
    data() {
        return {
            phoneNmuber: '',
            passwordNmuber: '',
            passwordVisible: false,
            passwordType: 'password',
            check: false,
            isChecked: false,
            login: false,
            showTips: false,
            z: 0,
            showLoginTip: false
        };
    },
    methods: {
        clickHandle: function () {
            if (this.isChecked) {
                this.login = true;
            } else {
                this.showTips = !this.showTips;
                this.z++;
            }
        },
        loginHandle: function () {
            // Toast.loading({
            //     message: '...',
            //     forbidClick: true,
            // });
            setTimeout(() => {
                // let token = 'MaJ6bKsj';
                // if (token) {
                //     //  token
                //     localStorage.setItem('token', token);
                //     // router.back() //  me
                //     this.$router.replace(this.$route.query.from);
                //     // console.log(this.$route.query.from);

                // } else {
                //     // 
                //     console.log('error');
                // }
                let token = 'MaJ6bKsj';
                if (this.phoneNmuber === '13612249522' && this.passwordNmuber === '123456') {
                    //  token
                    localStorage.setItem('token', token);
                    // router.back() //  me
                    this.$router.replace(this.$route.query.from);
                    // console.log(this.$route.query.from);

                } else {
                    // 
                    // console.log('error');
                    this.showLoginTip = true;
                }
            }, 2000);
        }
    }
};
</script>

