<template>
    <div class="playInterface">
        <div class="play">
            <div class="control">
                <h3>{{audio?.title}}</h3>
                <input type="range" id="range" min="0" :max="duration" :value="currentTime" @input="draging = true"
                    @change="draging = false; $emit('update-time', $event.target.value);">
                <div class="range_mask">
                    <div class="bar" :style="`width:${(340/duration)*tmpCurrentTime}rem`"></div>
                </div>
                <p class="remainingTime">
                    {{parseInt((duration-currentTime)/60%60)>9?parseInt((duration-currentTime)/60%60):'0'+parseInt((duration-currentTime)/60%60)}}
                    '
                    {{parseInt((duration-currentTime)%60)>9?parseInt((duration-currentTime)%60):'0'+parseInt((duration-currentTime)%60)}}''
                </p>
                <h6 class="user_name" v-if="audio?.author_list">{{audio?.author_list[0]?.user_name}}</h6>
                <h6 class="user_name" v-if="audio?.author">{{audio.author?.user_name}}</h6>
                <div class="play_btn">
                    <div class="prev" @click="changeAudio({dir:'prev', model: model})"></div>
                    <div class="play_pause">
                        <div class="play" v-if="!playing" @click="audioPlay(), $emit('audio-play')"></div>
                        <div class="pause" v-else @click="audioPause(), $emit('audio-pause')"></div>
                    </div>
                    <div class="next" @click="changeAudio({dir:'next', model: model})"></div>
                </div>
                <div class="bottom_btn">
                    <div class="play_type">
                        <div class="single" v-if="model===''" @click="model='';count++"></div>
                        <div class="list" v-else @click="model='';count++"></div>
                    </div>
                    <div class="bottom_tips">ONE</div>
                    <div class="collection">
                        <div class="add">
                            <span class="add_audio" v-show="!audioIDList.map(o => o).includes(audio.id)"
                                @click="addAudioList({id:audio.id, list:audio, type: audio.category}),add_P=true,remove_P=false,clickCount++"></span>
                            <span class="remove_audio" v-show="audioIDList.map(o => o).includes(audio.id)"
                                @click="removeAudioList({id:audio.id}),add_P=false,remove_P=true,clickCount++"></span>
                        </div>
                        <!-- <div class="info" @click="closePlayInterface()"></div> -->
                        <div class="info"
                            @click="$router.push({path:'/article', query:{type:audio.category, articleId: audio.item_id || audio.id || audio.content_id}}),closePlayInterface()">
                        </div>
                        <!-- <div class="info"
                            @click="clickInfo({ articleID:, audioID})">
                        </div> -->
                    </div>
                </div>
            </div>
        </div>
        <transition>
            <div class="model-tips" v-if="model==='' && count>1"></div>
        </transition>
        <transition>
            <div class="model-tips" v-if="model===''"></div>
        </transition>
        <div class="mask" @click="closePlayInterface()"></div>
        <!--  -->
        <div class="addPlayList" v-if="add_P"></div>
        <div class="removePlayList" v-if="remove_P && clickCount>0"></div>

        <div class="audio_tip" v-if="showAudioTip"></div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import 'vant/es/toast/style';
export default {
    props: {},
    data() {
        return {
            draging: false,
            tmpCurrentTime: this.currentTime,
            models: ["", ""],
            model: "",
            count: 0,
            add_P: false,
            remove_P: false,
            clickCount: 0,

        };
    },
    computed: {
        ...mapState(['audio', 'duration', 'currentTime', 'playing', 'audioList', 'audioIDList', 'showAudioTip', 'audioID'])
    },
    methods: {
        ...mapMutations(['closePlayInterface', 'changeCurrentTime', 'audioPlay', 'audioPause', 'addAudioList', 'removeAudioList', 'closeAudioTip', 'changeAudio', 'change_A_id', 'pause_A_id']),

        // addPlayList: function () {
        //     Toast({
        //         message: '',
        //         position: 'bottom',
        //     });
        // }
    },
    watch: {
        currentTime: function (n) {
            if (!this.draging) {
                // this.changeCurrentTime({n})
                this.tmpCurrentTime = n;
            }
        }
    },
    destroyed() {
        this.closeAudioTip();
    }
};
</script>

<style lang="less" scoped>
@keyframes switch {
    0% {
        opacity: 0;
    }

    25% {
        opacity: 1;
    }

    50% {
        opacity: 1;
    }

    75% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

.playInterface {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 9999;

    .play {
        width: 100%;
        height: 180rem;
        background-color: aliceblue;

        .control {
            width: 340rem;
            margin-left: 13rem;

            h3 {
                font-weight: normal;
                color: rgb(162, 161, 161);
                font-size: 14rem;
                width: 100%;
                text-align: center;
            }

            #range {
                width: 100%;
                position: relative;
                z-index: 9;
                opacity: 0;
            }

            .remainingTime {
                width: 100%;
                text-align: right;
                color: rgb(162, 161, 161);
            }

            .user_name {
                font-weight: normal;
                width: 100%;
                text-align: center;
                color: #ccc;
            }

            .play_btn {
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 15rem 0;

                &>div {
                    width: 30rem;
                    height: 30rem;
                    background-size: cover;
                }

                .prev {
                    background-image: url(../assets/last_disable.png);
                }

                .next {
                    background-image: url(../assets/next_disable.png);
                }

                .play_pause {
                    position: relative;
                    margin: 0 20rem;

                    &>div {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        background-size: cover;
                    }

                    .play {
                        background-image: url(../assets/player_play.png);
                    }

                    .pause {
                        background-image: url(../assets/player_pause.png);
                    }
                }
            }

            .bottom_btn {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;

                .play_type {
                    width: 22rem;
                    height: 22rem;
                    position: relative;

                    &>div {
                        width: 100%;
                        height: 100%;
                        background-size: cover;
                        position: absolute;
                        top: 0;
                        left: 0;
                    }

                    .single {
                        background-image: url(../assets/player_single_cycle.png);
                    }

                    .list {
                        background-image: url(../assets/player_all_cycle.png);
                    }
                }

                .bottom_tips {
                    flex-grow: 1;
                    text-align: center;
                    height: 30rem;
                    line-height: 30rem;
                    color: #ccc;
                    padding-left: 40rem;
                }

                .collection {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    .add {
                        width: 22rem;
                        height: 22rem;
                        margin-right: 20rem;
                        position: relative;

                        span {
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 22rem;
                            height: 22rem;
                            background-size: cover;
                        }

                        .add_audio {
                            background-image: url(../assets/music_collection_disable.png);
                        }

                        .remove_audio {
                            background-image: url(../assets/music_collection_disable_night.png);
                        }
                    }

                    .info {
                        width: 22rem;
                        height: 22rem;
                        background-image: url(../assets/fm_info.png);
                        background-size: cover;
                    }
                }
            }
        }
    }

    .mask {
        width: 100%;
        height: calc(100vh - 180rem);
        background-color: rgba(0, 0, 0, 0.647);
    }

    .model-tips {
        position: absolute;
        bottom: 100rem;
        left: 50%;
        width: 100rem;
        height: 30rem;
        line-height: 30rem;
        text-align: center;
        color: rgb(212, 208, 208);
        font-size: 12rem;
        background-color: rgba(0, 0, 0, 0.511);
        border-radius: 15rem;
        opacity: 0;
        margin-left: -50rem;
        animation: switch 1s linear;
    }

}

.addPlayList {
    position: absolute;
    bottom: 100rem;
    left: 135rem;
    padding: 10rem 15rem;
    background-color: rgba(0, 0, 0, 0.577);
    border-radius: 30rem;
    color: white;
    animation: switch 2s ease-in-out;
    opacity: 0;
}

.removePlayList {
    position: absolute;
    bottom: 100rem;
    left: 150rem;
    padding: 10rem 15rem;
    background-color: rgba(0, 0, 0, 0.577);
    border-radius: 30rem;
    color: white;
    animation: switch 2s ease-in-out;
    opacity: 0;
}

.audio_tip {
    position: absolute;
    bottom: 100rem;
    left: 112.5rem;
    padding: 10rem 15rem;
    background-color: rgba(0, 0, 0, 0.577);
    border-radius: 30rem;
    color: white;
    animation: switch 2s ease-in-out;
    opacity: 0;
}

.range_mask {
    width: 340rem;
    position: absolute;
    top: 56px;
    left: 15px;
    height: 4rem;
    background-color: rgba(0, 0, 0, 0.384);
    border-radius: 4rem;

    .bar {
        // width: 20rem;
        width: 0;
        height: 100%;
        background-color: black;
        border-radius: 4rem;
        position: relative;

        &::after {
            content: '';
            position: absolute;
            top: 2px;
            right: -5px;
            width: 10rem;
            height: 10rem;
            background-color: black;
            border-radius: 50%;
            margin-top: -5rem;
        }
    }

}
</style>
