<template>
    <div class="otherDates">
        <div class="lists">
            <h3>{{month}}</h3>
            <ul>
                <li v-for="(item, i) in everyDayLists" :key="item.id" :class="{active: i===currentDay}"
                    @click="currentDay=i, $emit('change-date',item.date), $emit('close-otherDates', false)">
                    <!-- $router.push({path: '/', query: {date:item.date}}) -->
                    <van-image v-if="!showOtherDays" width="155" height="110"
                        src="https://fastly.jsdelivr.net/npm/@vant/assets/cat.jpeg" />
                    <img v-else :src="`${item.cover}`" alt="">
                    <p>{{item.date}}</p>
                </li>
            </ul>
        </div>
        <transition name="date">
            <div class="date">
                <div class="current" v-if="!showSelectDate" @click="showSelectDate=true">
                    {{value.split('-')[0]+''+value.split('-')[1]+''}}</div>
                <van-datetime-picker v-else class="selectDate" :class="{ani: showSelectDate}" v-model="currentDate"
                    type="year-month" title="" :min-date="minDate" :max-date="maxDate" :formatter="formatter"
                    @confirm="changeDate(value); showSelectDate=false" @cancel="showSelectDate=false" />
                <!-- @confirm="$emit('change-date', date)"  -->
            </div>
        </transition>

        <div class="locadingmask" v-if="showLoading">
            <div class="locading"></div>
        </div>
    </div>
</template>

<script>
import { getEveryDays } from "@/apis";
import { ref } from 'vue';
import { mapState, mapMutations } from "vuex";
export default {
    props: {
        // everyDayLists: Array,
        showOtherDays: Boolean
    },
    data() {
        return {
            // month: new Date().getMonth() + 1,
            currentDay: 0,
            everyDayLists: [],
            showSelectDate: false,
        };
    },
    computed: {
        ...mapState(['showLoading'])
    },
    setup() {
        const currentDate = ref(new Date());

        const formatter = (type, val) => {
            if (type === 'year') {
                return `${val}`;
            }
            if (type === 'month') {
                return `${val}`;
            }
            return val;
        };
        // console.log(currentDate.value.getFullYear() + '-' + (currentDate.value.getMonth() + 1));
        return {
            minDate: new Date(2012, 0, 1),
            maxDate: new Date(2022, 9, 1),
            formatter,
            currentDate,
            month: currentDate.value.getMonth() + 1,
            value: currentDate.value.getFullYear() + '-' + (currentDate.value.getMonth() + 1),
        };
    },
    methods: {
        changeDate: function (value) {
            this.isShowLoading();
            getEveryDays(value).then(res => {
                this.everyDayLists = res.data.data;
                // console.log(value);
                this.closeshowLoading();
            });
        },
        ...mapMutations(['isShowLoading', 'closeshowLoading'])
    },
    created() {

        this.changeDate(0);
    }
};
</script>

<style lang="less" scoped>
@keyframes date {
    from {
        margin-top: 0;
    }

    to {
        margin-top: -250rem;
    }
}

// .date-enter-active {
//     animation: date .3s linear;
// }

// .date-leave-active {
//     animation: date .3s linear reverse;
// }
@keyframes loading {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

@keyframes xxx {
    from {
        background-position-x: 0;
    }

    to {
        background-position-x: -2002px;
    }
}

.locadingmask {
    position: fixed;
    top: 0rem;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #fff;
    z-index: 999;

    .locading {
        width: 154rem;
        height: 155rem;
        background-image: url("../assets/_20221007141751.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        animation-name: xxx;
        animation-duration: 1s;
        animation-iteration-count: infinite;
        /* animation-timing-function: linear; */
        animation-timing-function: steps(13, end);
        margin-top: 278rem;
        margin-left: 125rem;

    }
}

.otherDates {
    width: 100%;
    height: 89vh;
    overflow: hidden;
    // background-color: aqua;
    margin-top: 70rem;

    &::-webkit-scrollbar {
        display: none;
        width: 0 !important;
        height: 0 !important;
        -webkit-appearance: none;
        background: transparent;
    }

    .lists {
        height: 83vh;
        overflow: auto;

        h3 {
            width: 100%;
            text-align: center;
            margin-bottom: 20rem;
            font-size: 14rem;
            color: #a7a7a7;
            font-weight: normal;
        }

        ul {
            width: 345rem;
            margin-left: 15rem;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;

            li {
                width: 155rem;
                height: 155rem;
                box-shadow: 0 0 0 1rem rgb(234, 232, 232);
                margin-bottom: 20rem;

                img {
                    width: 100%;
                    height: 110rem;
                    background-size: cover;
                    background-position: center;
                    filter: blur(0.7rem);
                    display: block;
                }

                p {
                    width: 100%;
                    color: #a7a7a7;
                    text-align: center;
                    padding-bottom: 10rem;
                    font-size: 14rem;
                }

                &.active {
                    box-shadow: 0 0 0 1rem gray;
                }
            }
        }
    }

    .date {
        margin-top: 15rem;

        .current {
            width: 100%;
            text-align: center;
            font-size: 16rem;
        }

        .ani {
            animation: date .3s linear;
        }

        .selectDate {
            margin-top: -250rem;
        }
    }
}
</style>
